<template>
    <div class="layout-px-spacing">  
        <ul class="navbar-nav flex-row mt-2">
            <li>
                <div class="page-header">
                    <nav class="breadcrumb-one" aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><a href="javascript:;">Masters</a></li>
                            <li class="breadcrumb-item active" aria-current="page"><span> vendor </span></li>
                        </ol>
                    </nav>
                </div>
            </li>
        </ul>

           <div class="d-flex me-4 mt-2 ">
                        <div class="ms-auto">
                            <button class="btn btn-primary me-2 " @click="register_vendor =! register_vendor">                           
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-plus-square" viewBox="0 0 16 16">
  <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2z"/>
  <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
</svg>
</button>
                            <button class="btn btn-success "
                             @click="printReport()" 
                            >                           
                              <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
  <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1"/>
  <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1"/>
</svg>
</button>
 
                        </div>
                    </div>

       
         <!-- vendor registration form starts -->
 <form v-if="register_vendor" class="mt-3 p-3 border rounded bg-white shadow" @submit.prevent="registerVendor">
        <h4 class="text-center">REGISTER VENDOR</h4>
        <div class="row mb-2">
    
            <div class="col-md-4">
                <label class="form-label">Vendor Name</label>
                <input type="text" class="form-control" placeholder="Enter Vendor Name" v-model="name" required>
            </div>

            <div class="col-md-4">
                <label class="form-label">Email</label>
                <input type="email" class="form-control" placeholder="Enter Email" v-model="email" >
            </div>

            <div class="col-md-4">
                <label class="form-label">Phone</label>
                <input type="text" class="form-control" placeholder="Enter Phone Number" v-model="phone" required>
            </div>

        </div>
        <div class="row mb-2">
            <div class="col-md-6">
                <label class="form-label">Address</label>
                <input type="text" class="form-control" placeholder="Enter Address" v-model="address" required>
            </div>

            <div class="col-md-6">
                <label class="form-label">Location</label>
                <input type="text" class="form-control" placeholder="Enter Location" v-model="location" required>
            </div>
        </div>
        <div class="text-center gap-2 justify-content-end">
            <button type="button" class="btn btn-danger me-3" @click="register_vendor = false; resetForm();">Cancel</button>
            <button type="submit" class="btn btn-primary" :disabled="loading">
                <span v-if="loading" class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
                <span v-else>Register</span>
            </button>
        </div>
    </form>
             <!-- vendor registration form ends-->



              <!-- loading spinner starts -->
        <div v-if="loading_spinner" v-html="tableShimmer()"></div>
        <!-- Loading spinner ends -->
        <div v-else class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="vendor" :columns="columns" :options="table_option">
                            <template #actions="props">
                                <div class="actions text-center">

                                    <a href="javascript:;" class="edit me-3" @click="openEditModal(props.row)" data-bs-toggle="modal" :data-bs-target="'#vendor-edit-modal'">
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="24"
                                            height="24"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            stroke-width="1.5"
                                            stroke-linecap="round"
                                            stroke-linejoin="round"
                                            class="text-success"
                                        >
                                            <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                                            <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
                                        </svg>
                                    </a>
                                </div>                                 
                            </template>
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>

             <!-- Edit vendor modal starts -->
<div class="modal fade mymodal" id="vendor-edit-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="VendorEditModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="VendorEditModalLabel">Edit Vendor</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"  id="close_editing"></button>
      </div>
      <form @submit.prevent="updateVendor()">
        <div class="modal-body">
          <div class="row mb-3">
            <div class="col-md-4">
              <label class="form-label">Vendor Name</label>
              <input type="text" class="form-control" v-model="name" required>
            </div>
            <div class="col-md-4">
              <label class="form-label">Email</label>
              <input type="email" class="form-control" v-model="email" >
            </div>
            <div class="col-md-4">
              <label class="form-label">Phone</label>
              <input type="text" class="form-control" v-model="phone" required>
            </div>
            <div class="col-md-6">
              <label class="form-label">Address</label>
              <input type="text" class="form-control" v-model="address" required>
            </div>
            <div class="col-md-6">
              <label class="form-label">Location</label>
              <input type="text" class="form-control" v-model="location" required>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="resetForm()" id="close_editing">Close</button>
          <button type="submit" class="btn btn-primary" :disabled="loading">
            <span v-if="loading" class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
            <span v-else>Update</span>
          </button>
        </div>
      </form>
    </div>
  </div>
</div>
<!-- Edit vendor modal ends -->

    </div>
</template>

<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>

<script setup>
    import { onMounted, ref, inject } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    
    useMeta({ title: 'vendor' });  
    
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const tableShimmer = inject('tableShimmer');
    const loadingSpinner = inject('loadingSpinner'); 
    const register_vendor = ref(false);
    
    const loading_spinner = ref(false);
    const columns = ref(['SNo','name','email','phone','address','location','actions']);    
    const name = ref('');
    const email = ref('');
    const phone = ref('');
    const address = ref('');
    const location = ref('');
    const vendor = ref([]);
  


    
    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50 ,100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['SNo', 'name','email','phone','address','location'],        
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });


    const getVendors = async () => {
        loading_spinner.value = true;
        try {
            const response = await axiosInstance.get('vendors');
            vendor.value = response.data.map((item, index) => ({
                ...item,
                SNo: index + 1,
                id: item.id,
                name:item.name,
                email:item.email,
                phone:item.phone,
                address:item.address,
                location:item.location,                 
            }));
        } catch (error) {
            showError(error.message);
        }finally{
            loading_spinner.value = false;
        }
    };

    const loading = ref(false);
    
    const registerVendor = async () => {
        loading.value = true;
        const payload = {
           name: name.value,
           phone:phone.value,
           email:email.value,
           address:address.value,
           location:location.value,
        };

        try {
            const response = await axiosInstance.post('vendors', payload);
            getVendors();
            showMessage('Vendor registered successfully!');
            resetForm();
        } catch (error) {
          console.log(error);
            showError(error.message);
        } finally {
            loading.value = false;
        }
    };

    const resetForm = () => {
   register_vendor.value = false;
    name.value = '';
    email.value = '';
    phone.value = '';
    address.value = '';
    location.value = '';
};

const editId = ref(null);
const openEditModal = (row) => {
    editId.value = row.id;
    name.value = row.name;
    email.value = row.email;
    phone.value = row.phone;
    address.value = row.address;
    location.value = row.location;
};

   const updateVendor = async () => {
    loading.value = true; 
    const payload = {
        name: name.value,
        phone: phone.value,
        email: email.value,
        address: address.value,
        location: location.value,
    };

    try {
        const response = await axiosInstance.put(`vendors/${editId.value}`, payload);
        getVendors();
        showMessage('Vendor updated successfully!');
        document.getElementById('close_editing').click();
    } catch (error) {
      console.log(error);
        showError(error.message);
    } finally {
        loading.value = false;
    }
};

const printReport = async () => {
  try {
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    document.body.appendChild(iframe);

    const printContent = `
<!DOCTYPE html>
<html>
<head>
<title>Vendors List</title>
<style>
  body { font-family: 'Courier New', monospace; margin: 40px; font-size: 11px; }
  .text-style { font-family: 'Courier New', monospace; }
  .company-title { text-align: center; }
  .title { text-align: center; font-size: 18px; margin: 10px 0; }
  table { width: 95%; border-collapse: collapse; margin: 10px auto; font-size: 11px; }
  th, td { border: 0.5px solid #000; padding: 4px; }
  th { background-color: #f8f8f8; font-weight: bold; }
  .text-right { text-align: right; }
  .text-center { text-align: center; }
  hr { border: 0; border-top: 0.5px solid #000; margin: 0px 0; }
  @media print { body { margin: 10mm; } }
</style>
</head>
<body>

<div class="company-title text-style">
  <i>
  <h1>COPEX CONTRACTORS CO.LTD</h1>
  <div><b>Address:</b> Msamvu, P.O.BOX 678, Morogoro</div>
  <div><b>Tel:</b> 0713 000 777, 0657 111 555</div>
  <div><b>Email:</b> info@copexcontractors.co.tz</div>
  <div><b>TIN:</b> 135-489-247 &nbsp;&nbsp; <b>VRN:</b> 40-032826C</div>
  </i>
</div>

<hr style="margin-top:20px">

<div class="title text-style">
  VENDORS LIST
</div>

<hr>

<table>
  <thead>
    <tr>
      <th>No.</th>
      <th>Name</th>
      <th>Email</th>
      <th>Phone</th>
      <th>Address</th>
      <th>Location</th>
    </tr>
  </thead>
  <tbody>
    ${vendor.value.map((v, index) => `
    <tr>
      <td>${index + 1}</td>
      <td>${v.name || '-'}</td>
      <td>${v.email || '-'}</td>
      <td>${v.phone || '-'}</td>
      <td>${v.address || '-'}</td>
      <td>${v.location || '-'}</td>
    </tr>
    `).join('')}
  </tbody>
</table>

</body>
</html>
`;

    const iframeDoc = iframe.contentWindow.document;
    iframeDoc.open();
    iframeDoc.write(printContent);
    iframeDoc.close();

    iframe.onload = () => {
      iframe.contentWindow.focus();
      iframe.contentWindow.print();
      setTimeout(() => {
        document.body.removeChild(iframe);
      }, 1000);
    };
  } catch (error) {
    showError(`Failed to print report: ${error.message}`);
  }
};
    
    onMounted(() => {
        getVendors();
       
    });
</script>